        <!-- close button -->
        <div class="user-info-close" @click="closeUserInfo"><i class="fa-light fa-xmark"></i></div>
        
        <div class="user-info-title">
            Smart ways to save energy at Expo 2020
        </div>

        <div>
            <div class="user-info-subtitle">
